   @include media('>medium') {\n      flex-direction: row;\n      justify-content: space-between;\n      text-align: left;\n    }\n  }\n\n  &__icon {\n    margin-bottom: $space;\n\n    @include media('>medium') {\n      margin-bottom: 0;\n      margin-right: $space-double;\n    }\n  }\n\n  &__body {\n    flex: 1;\n    margin-bottom: $space;\n\n    @include media('>medium') {\n      margin-bottom: 0;\n      margin-right: $space;\n    }\n  }\n\n  &__form {\n    @include media('>medium') {\n      min-width: 320px;\n    }\n\n    @include media('>xlarge') {\n      min-width: 400px;\n    }\n  }\n}",".c-paint-colors {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  flex-direction: column;\n\n  @include media('>medium') {\n    flex-direction: column;\n    align-items: flex-start;\n  }\n\n  &__items {\n    display: flex;\n  }\n\n  &__paint-color {\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    margin: 0 $space-half;\n    text-align: center;\n  }\n\n  &__color {\n    display: block;\n    height: 90px;\n    width: 90px;\n    min-width: 90px;\n    border-radius: 90px;\n    border: $border--standard;\n    margin-bottom: $space-half;\n  }\n}",".c-progress-bar {\n  margin: 0;\n  padding: 0;\n  position: sticky;\n  width: 100%;\n  top: calc(#{$header-utility-height} + #{$header-height});\n  left: 0;\n  display: flex;\n  z-index: 996;\n\n  progress {\n    height: 10px;\n    appearance: none;\n    border: none;\n    color: $c-primary;\n    transition: $transition-all;\n    background-color: $c-gray;\n\n    &[value] {\n      appearance: none;\n      width: 100%;\n      height: 10px;\n      transition: all 0.15s ease;\n      color: $c-primary;\n    }\n\n    &::-moz-progress-bar {\n      background-color: $c-gray;\n    }\n\n    &::-webkit-progress-bar {\n      background-color: $c-gray;\n      width: 100%;\n    }\n\n    &::-webkit-progress-value {\n      background: $c-primary;\n    }\n  }\n}\n\n.hide-progress-bar .c-progress-bar