rd;\n    margin-bottom: $space-one-fourth;\n\n    @include media('>medium') {\n      flex-direction: column;\n      padding: 0 $space-double;\n      margin-bottom: 0;\n      border: 0;\n    }\n  }\n\n  &__heading {\n    padding-bottom: $space-one-fourth;\n  }\n}",".c-steps {\n  &__item {\n    display: flex;\n    justify-content: flex-start;\n    align-items: flex-start;\n  }\n\n  &__counter {\n    position: sticky;\n    top: 0;\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n    min-height: 200px;\n    min-width: 30px;\n    margin-right: $space-double;\n    justify-content: space-between;\n\n    &-index,\n    &-step {\n      line-height: 1;\n      display: block;\n      z-index: 1;\n      position: relative;\n      background-color: $c-white;\n    }\n\n    &-index {\n      font-size: 72px;\n      font-family: $font-secondary;\n    }\n\n    &-step {\n      transform: rotate(-90deg);\n      position: absolute;\n      bottom: 13px;\n      width: 42px;\n      height: 16px;\n    }\n\n    &::before {\n      content: \"\";\n      display: block;\n      position: absolute;\n      margin: auto;\n      left: 0;\n      right: 0;\n      top: 0;\n      width: 1px;\n      height: 100%;\n      background-color: $c-black;\n      z-index: 0;\n    }\n  }\n\n  &__image,\n  &__images {\n    padding-top: $space-half;\n    background-color: $c-white;\n  }\n\n  &__images {\n    display: flex;\n    flex-direction: column;\n    width: 100vw;\n    left: 50%;\n    right: 50%;\n    margin-left: calc(-50vw - #{$space-double} + 5px);\n    margin-right: calc(-50vw - #{$space-double} + 5px);\n    position: relative;\n    background-color: $c-white;\n    padding-top: $space-half;\n\n    @include media('>medium') {\n      flex-direction: row;\n    }\n\n    > * {\n      padding-bottom: $space;\n\n      @include media('>medium') {\n        padding-right: $space;\n      }\n\n\n      &:last-child {\n        padding-bottom: 0;\n\n        @include media('>medium') {\n          padding-right: 0;\n        }\n      }\n    }\n\n    figcaption {\n      padding-left: $space;\n    }\n  }\n}","$tabs-height: 350px;\n\n.c-tabs {\n  background: $c-white;\n  position: relative;\n  display: block;\n  border-left: $border--standard;\n  border-right: $border--standard;\n  border-bottom: $border--standard;\n\n  @include media('>medium') {\n    min-height: $tabs-height;\n  }\n\n  &__title {\n    padding: 0 $space;\n    min-height: 70px;\n    height: 70px;\n    display: flex;\n    align-items: center;\n    background-color: $c-gray;\n    border-top: $border--standard;\n    top: 0;\n    cursor: pointer;\n    transition: $transition-all;\n\n    @include media('>medium') {\n      position: absolute;\n      top: 0;\n      width: 30%;\n      border-right: $border--standard;\n    }\n\n    > * {\n      font-weight: 600;\n      color: $c-gray;\n    }\n\n    &:first-child {\n      border-top: none;\n    }\n\n    &.is-active {\n      background-color: $c-white;\n      border-right-color: $c-white;\n\n      > * {\n        color: $c-gray;\n      }\n\n      &:first-child {\n        @include media('<=medium') {\n          border-bottom: $border--standard;\n        }\n      }\n\n      + .c-tabs__content {\n        display: flex;\n        background-color: $c-white;\n      }\n\n      &:hover {\n        background-color: $c-white;\n      }\n    }\n\n    &:hover {\n      background-color: $c-gray;\n\n      > * {\n        color: $c-black;\n      }\n    }\n  }\n\n  &__content {\n    display: none;\n    flex-direction: column;\n    align-items: stretch;\n    justify-content: space-between;\n\n    @include media('>small') {\n      flex-direction: row;\n    }\n\n    @include media('>medium') {\n      margin-left: 30%;\n      height: $tabs-height;\n    }\n\n    > * {\n      @include media('>small') {\n        width: 50%;\n      }\n\n      @include media('>medium') {\n        width: 70%;\n      }\n    }\n\n    &-block {\n      padding: $space-double $space;\n      order: 1;\n\n   